.slider-box {
  padding: 28px 0 181px;
  background-color: $background-color-4;
  &--header {
    font-size: 25px;
    margin-left: 33px;
    font-family: $header-font;
    @media (min-width: 720px) {
      font-size: 47px;
    }
  }
  &--subheader {
    font-size: 20px;
    text-transform: uppercase;
    margin-left: 37px;
    margin-top: 9px;
    font-style: italic;
    @media (min-width: 720px) {
      font-size: 32px;
    }
  }
  &__element {
    text-align: center;
    font-size: 26px;
    text-transform: uppercase;
    &__icon {
      height: 116px;
      width: 116px;
      border-radius: 110px;
      margin-left: auto;
      margin-right: auto;
      box-shadow: 0 0 0 10px #4260a8, 0 0 0 20px #8194be, 0 0 0 30px #adb8d4;
      margin-top: 34px;
      line-height: 123px;
      font-size: 55px;
      text-indent: -5px;
      color: #4260a8;
      &--education {
        background-image: url(../img/book-icon.png);
        background-size: 71px;
        background-repeat: no-repeat;
        background-position: center;
      }
    }
    &__text {
      margin-top: 63px;
    }
  }
}

.carousel-slider {
  margin-left: auto;
  margin-right: auto;
  margin-top: 76px;
  @media (min-width: 1200px) {
    width: 1154px;
  }
}
.slick-prev {
  left: 20px;
}

.slick-next {
  right: 20px;
}

@media (min-width: 1200px) {
  .slick-prev {
    left: -41px;
  }
  .slick-next {
    right: -6px;
  }
  .slick-slide {
    width: 278px;
  }
}

.slick-prev, .slick-next {
  top: 36%;
}

.slick-prev:before, .slick-next:before {
  color: #d8d8d8;
  font-family: "Font Awesome 5 Pro";
  font-size: 55px;
}

